@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    180.2deg,
    rgb(30, 33, 48) 6.8%,
    rgb(74, 98, 110) 131%
  );
  @apply mx-auto h-full w-full max-w-[1340px] bg-app-dark-blue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  @apply rounded-full  border-primary  text-primary py-2 px-4 uppercase text-xs font-bold tracking-wider cursor-pointer hover:bg-primary hover:text-white transition ease-out duration-500;
}

.btn_hover {
  @apply border-2 border-secondary-300;
}
.btn_hover:hover {
  @apply border-2 border-primary drop-shadow-lg;
}

