.gradient_border {
  border-left: 2px solid #e4510d;
  border-right: 2px solid #5a55e1;
  background-image: linear-gradient(90deg, #272568, #e4510d),
    linear-gradient(90deg, #d46430, #36338a);
  background-size: 100% 3px;
  background-position: 0 0, 0 100%;
  background-repeat: no-repeat;
}

.gradient_back {
  background-image: linear-gradient(90deg, #272568, #e4510d),
    linear-gradient(90deg, #d46430, #36338a);
}

.btn-rounded {
  @apply bg-secondary-200;
  @apply border-2 border-secondary-100 p-1;

  border-radius: 50%;
}
.btn-rounded:hover {
  @apply bg-primary;

  box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}
.card-list-wrapper {
  @apply grid 2xs:grid-cols-2 grid-cols-1 gap-x-6 gap-y-3  md:grid-cols-3 lg:grid-cols-4;
}

.bg_translucent {
  background-image: linear-gradient(
    to bottom,
    rgba(245, 246, 252, 0.52),
    rgba(117, 19, 93, 0.3)
  );

  background-size: cover;
  color: white;
  padding: 3px;
}

.tab_genre {
  @apply ml-2 rounded-md border-2 py-px px-2 text-[8px] font-medium uppercase tracking-wider text-app-pure-white sm:ml-4 sm:text-[10px];
}

::-webkit-scrollbar {
  width: 0.9rem; /* Mostly for vertical scrollbar */
  height: 10px; /* Mostly for horizontal scrollbar */
}
::-webkit-scrollbar-thumb {
  /* Foreground */
  border-radius: 10px;
  padding: 5px;
  background-image: linear-gradient(45deg, #272568, #e4510d),
    linear-gradient(90deg, #d46430, #36338a);
  -webkit-box-shadow: 0px 0px 20px #ffb694;
  -moz-box-shadow: 0px 0px 20px #ffb694;
  box-shadow: 0px 0px 20px #ffb694;
}
::-webkit-scrollbar-track {
  /* Background */
  /* background: #afafaf;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.8); */

  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0) 50%,
    #d46430 45%,
    #d46430 55%,
    rgba(0, 0, 0, 0) 55%,
    rgba(0, 0, 0, 0) 100%
  );
}

::-webkit-scrollbar-corner {
  background-color: white;
}
